@import url('https://fonts.googleapis.com/css2?family=Montserrat:ital,wght@0,100..900;1,100..900&display=swap');

.topcontent{
    color: white;
    position: relative;
}
.topcontent__container{
    position: absolute;
    top: 100px;
    right: 70px;
}

.topcontent__container > p{
    letter-spacing: 1px;
    text-align: left;
    padding-left: 3px;
    padding-top: 20px;
}
.topcontent__container > a > button{
    margin-top: 20px;
    text-align: left;
    margin-left: 5px;
    border-radius: 5px;
    border: none;

}
.topcontent__container > h1{
    font-size: 40px;
    font-family: "Montserrat",sans-serif;
}
.topcontent__container > h1 >span{
    color: blue;
}


.topcontent__resumebutton{
    color: white;
    background-color: blue;
    font-size: 14px;
    padding: 5px 30px;
    font-family: "Montserrat",sans-serif;
    font-weight: 300;
    cursor: pointer;
    margin-right: 20px;

}

.topcontent__workbutton{
    color: white;
    border: 1px solid white !important;
    background-color: transparent;
    font-size: 14px;
    padding: 5px 30px;
    font-weight: 300;
    cursor: pointer;
    border-radius: 5px;
}

.topcontent__resumebutton:hover{
    color: blue;
    background-color: white;
    font-weight: 600;
}

.topcontent__workbutton:hover{
    color: black;
    background-color: white;
    font-weight: 600;
}